import React from 'react';
import { connect } from 'react-redux';
import * as actions from '../../actions';

const formatMoney = (valor) => 'R$ '+Number(valor).toFixed(2).replace('.',',');

const Input = (props) => (
    <div className="form-group">
        {props.label && <label>{props.label}</label>}
        <input type={props.type || 'text'} name={props.name} placeholder={props.placeholder} onChange={props.onChange} value={props.value} className="form-control" />
    </div>
)

const DEFAULT_STATE = {
    nome: "",
    cpf: "",
    endereco: "",
    telefone: "",

    metodoPagamento: '',
    parcelas: '',

    produtos: [
        { quantidade: '', produto: '', valor: '0' }
    ],
    errors: []
};

class AlterarPedido extends React.Component {

    constructor(props){
        super();
        const produtos = props.pedido.pedido;
        this.state = props.pedido ? Object.assign({}, props.pedido.cliente, {produtos}, props.pedido.formaDePagamento) : DEFAULT_STATE
    }

    componentWillUpdate(nextProps){
        if( !this.props.pedido && nextProps.pedido ){
            const produtos = nextProps.pedido.pedido;
            this.setState( Object.assign({}, nextProps.pedido.cliente, {produtos}, nextProps.pedido.formaDePagamento) )
        }
    }

    handleSubmit(){
        if(window.confirm('Você tem certeza que deseja alterar este pedido?')){
            this._handleSubmit()
        }
    }

    validate(){
        const {state} = this;
        const required = [ 
            { field:'nome', label: "Empresa/Nome" },
            { field:'cpf', label: "CNPJ/CPF" }, 
            { field:'endereco', label: "Endereço" },
            { field:'telefone', label: "Telefone" },
            { field:'metodoPagamento', label: "Metódo de Pagamento" },
            { field:'parcelas', label: "Parcelas" }, 
            { field:'produtos', label: "Produtos" } 
        ];
        const errors = [];
        required.forEach((item) => {
            if(!state[item.field]) errors.push(item.label)
        });
        this.setState({ errors });
        return !( errors.length > 0 );
    }
    
    _handleSubmit(){
        const { nome, cpf, endereco, telefone, metodoPagamento, parcelas, produtos } = this.state
        const { pedido } = this.props

        if( !this.validate() ) return;

        pedido.cliente = { nome, cpf, endereco, telefone };
        pedido.pedido = produtos;

        const valor = produtos.reduce((valorTotal, produto) => valorTotal + ( parseFloat( produto.valor ,10) * parseFloat(produto.quantidade, 10) ) , 0);
        pedido.formaDePagamento = { metodoPagamento, parcelas, valorTotal: valor };

        this.props.updatePedido(pedido.id, pedido)
        this.props.toogleForm()
    }

    addProduto(){
        const { produtos } = this.state;
        produtos.push({ quantidade: '', produto: '', valor: '0' })
        this.setState({ produtos })
    }
    removeProduto(_idx){
        let { produtos } = this.state;
        produtos = produtos.filter((_i, idx) => idx !== _idx)
        this.setState({ produtos })
    }

    onChange(ev, field){
        this.setState({ [field]: ev.target.value })
        this.validate()
    }
    onChangeProduto(ev, field, index){
        let { produtos } = this.state;
        produtos[index][field] = ev.target.value;
        this.setState({ produtos })
    }

    renderError(){
        const { errors } = this.state
        if( !errors || errors.length === 0 ) return null;
        let errorMessage = 'Preencha os campos: ';
        errors.forEach((_error, idx) => {
            errorMessage += _error;
            if(idx !== errors.length - 1) errorMessage+=', ';
            else errorMessage+='.';
        })
        return (
            <div className="alert alert-danger">
                { errorMessage }
            </div>
        )
    }

    renderAlteraredido(){
        const { nome, cpf, endereco, telefone, metodoPagamento, parcelas, produtos } = this.state
        const valor = produtos.reduce((valorTotal, produto) => valorTotal + ( parseFloat( produto.valor ,10) * parseFloat(produto.quantidade, 10) ) , 0);
        return (
            <div>
                <h3>Novo Pedido</h3>

                    <Input type="text" name="nome" placeholder="Empresa/Nome" value={nome} onChange={(ev) => this.onChange(ev, 'nome')} />
                    <Input type="text" name="cpf" placeholder="CNPJ/CPF" value={cpf} onChange={(ev) => this.onChange(ev, 'cpf')} />
                    <Input type="text" name="endereco" placeholder="Endereço" value={endereco} onChange={(ev) => this.onChange(ev, 'endereco')} />
                    <Input type="text" name="telefone" placeholder="Telefone" value={telefone} onChange={(ev) => this.onChange(ev, 'telefone')} />

                    <hr />

                    {
                        produtos.map((item, idx) => (
                            <div className="flex horizontal-only width-90" key={idx}>
                                <div className="flex-2">
                                    <Input type="number" name="quantidade" label="Quantidade" value={item.quantidade} onChange={(ev) => this.onChangeProduto(ev, 'quantidade', idx)} />
                                    &nbsp;
                                </div>
                                <div className="flex-4">
                                    <Input type="text" name="produto" label="Produto" value={item.produto} onChange={(ev) => this.onChangeProduto(ev, 'produto', idx)} />
                                </div>
                                <div className="flex-3">
                                    <Input type="number" name="valor" label="Valor Und." value={item.valor} onChange={(ev) => this.onChangeProduto(ev, 'valor', idx)} />
                                </div>
                                <div className="flex-1 flex flex-center">
                                    <button className="btn btn-red" onClick={() => this.removeProduto(idx)}>
                                            -
                                    </button>
                                </div>
                            </div>
                        ))
                    }
                    <button className="btn btn-green" onClick={() => this.addProduto()}>
                        +
                    </button>
                    <p>Valor do Pedido: {formatMoney(valor)}</p>
                    <hr />

                    <Input type="text" name="metodoPagamento" label="Metodo de Pagamento" value={metodoPagamento} onChange={(ev) => this.onChange(ev, 'metodoPagamento')} />
                    <Input type="number" name="parcelas" label="Parcelas" value={parcelas} onChange={(ev) => this.onChange(ev, 'parcelas')} />

                    <br />
            </div>
        )
    }

    render(){
        return (
            <div className="NovoPedido">    
                { this.renderAlteraredido() }
                { this.renderError() }
                <button className="btn btn-blue" onClick={() => this.handleSubmit()}>Salvar</button>
                <button className="btn" onClick={() => this.props.toogleForm() }>Cancelar</button>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    pedido: state.pedidos.pedido
})

export default connect(mapStateToProps, actions)(AlterarPedido)


