import React from 'react';
import { connect } from 'react-redux';
import * as actions from '../../actions';
import AlterarPedido from '../AlterarPedido';

const formatMoney = (valor) => 'R$ '+Number(valor).toFixed(2).replace('.',',');

class Pedido extends React.Component {
    state = { showForm: false }
    toogleForm = () => this.setState({ showForm: !this.state.showForm })

    renderCliente({ nome, endereco, telefone, cpf }){
        return (
            <div>
                <p>Empresa/Nome: {nome}</p>
                <p>Endereço: {endereco}</p>
                <p>Telefone: {telefone}</p>
                <p>CNPJ/CPF: {cpf}</p>
            </div>
        )
    }

    renderPedido(produtos){
        const valorTotal = produtos.reduce((valorTotal, produto) => valorTotal + ( parseFloat( produto.valor ,10) * parseFloat(produto.quantidade, 10) ) , 0);
        return (
            <div>
                <div className="flex horizontal-only width-90">
                    <div className="flex-2">
                        Qtd.
                    </div>
                    <div className="flex-4">
                        Produto
                    </div>
                    <div className="flex-2">
                        Valor
                    </div>
                </div>
                {
                    produtos.map((item, idx) => (
                        <div className="flex horizontal-only width-90" key={idx}>
                            <div className="flex-2">
                                <p>{item.quantidade}</p>
                            </div>
                            <div className="flex-4">
                                <p>{item.produto}</p>
                            </div>
                            <div className="flex-2">
                                <p>{formatMoney(item.valor)}</p>
                            </div>
                        </div>
                    ))
                }
                <br />
                <p>Valor Total: {formatMoney(valorTotal)}</p>
            </div>
        )
    }

    renderFormaDePagamento({ metodoPagamento, parcelas }){
        return (
            <div>
                <p>Metódo de Pagamento: {metodoPagamento}</p>
                <p>Parcelas: {parcelas}</p>
            </div>
        )
    }

    _removePedido(id){
        if(window.confirm('Você tem certeza que deseja remover este pedido?')){
            this.props.removePedido(id)
        }
    }

    renderButtons(pedido){
        return (
            <div className="flex horizontal-only">
                <div className="flex-1 flex flex-center">
                    <button className="btn btn-green" onClick={() => this.toogleForm()}>
                        Alterar
                    </button>
                </div>
                <div className="flex-1 flex flex-center">
                    <button className="btn btn-red" onClick={()=> this._removePedido(pedido.id)}>
                        Remover
                    </button>
                </div>
            </div>
        )
    }

    renderPlane(){
        const { pedido } = this.props
        return (
            <div className="Pedido container">    
                <p className="anchor" onClick={() => this.props.getPedido(null)}>{"< Voltar"}</p>
                <h2>Pedido</h2>
                <hr />
                { this.renderCliente(pedido.cliente) }
                <hr />
                { this.renderPedido(pedido.pedido) }
                <hr />
                { this.renderFormaDePagamento(pedido.formaDePagamento) }
                <br />
                { this.renderButtons(pedido) }
            </div>
        );
    }

    render(){
        const { showForm } = this.state         
        return showForm ? <AlterarPedido toogleForm={() => this.toogleForm()} /> : this.renderPlane();
    }
}

const mapStateToProps = state => ({
    pedido: state.pedidos.pedido
})

export default connect(mapStateToProps, actions)(Pedido)


