import React from 'react';
import { connect } from 'react-redux';

import NovoPedido from './NovoPedido';
import ListaDePedidos from './ListaDePedidos';

import Pedido from './Pedido';

class Aplicacao extends React.Component {

    renderPedidos(){
        return (
            <div>
                <h2>Pedidos</h2>
                <NovoPedido />
                <ListaDePedidos />
            </div>
        )
    }

    renderPedido(){
        return (
            <div>
                <Pedido />
            </div>
        )
    }

    renderRoutes(){
        const { pedido } = this.props
        return (!pedido) ? this.renderPedidos() : this.renderPedido ();
    }

    render(){
        return (
            <div className="Aplicacao">
                { this.renderRoutes() }
            </div>
        )
    }
}

const mapStateToProps = state => ({
    pedido: state.pedidos.pedido
})

export default connect(mapStateToProps)(Aplicacao);


