import {
    GET_PEDIDOS,
    NEW_PEDIDO,
    UPDATE_PEDIDO,
    REMOVE_PEDIDO,
    GET_PEDIDO
} from './types';

// HELPERS
const generateRandomId = () => Math.floor(Math.random() * 200000000 + 100000000).toString();

//  LOCAL STORE
const _setLocal = (_data, _type) => localStorage.setItem(_type, JSON.stringify(_data));
const _getLocal = (_type) => JSON.parse(localStorage.getItem(_type));
const _getPedidos = () => _getLocal('@app:pedidos');
const _setPedidos = (_data) => _setLocal(_data,'@app:pedidos');
// const _getPedido = (_id) => {
//     let _pedidos = _getPedidos();
//     return _pedidos.reduce((all, pedido) => pedido.id === _id ? pedido : all, {});
// }
const _newPedido = (_pedido) => {
    const _pedidos = _getPedidos() || [];
    _pedidos.push(_pedido);
    _setPedidos(_pedidos);
}
const _updatePedido = (_id, _pedido) => {
    let _pedidos = _getPedidos() || [];
    _pedidos = _pedidos.map((pedido) => (pedido.id === _id) ? _pedido : pedido );
    _setPedidos(_pedidos);
}
const _removePedido = (_id) => {
    let _pedidos = _getPedidos() || [];
    _pedidos = _pedidos.filter((pedido) => pedido.id !== _id);
    _setPedidos(_pedidos);
}

// ACTIONS
export const getPedidos = () => {
    return { type: GET_PEDIDOS, pedidos: _getPedidos() || []}
}

export const getPedido = (pedido) => ({ type: GET_PEDIDO, pedido})

export const newPedido = (_cliente, _pedido, _formaDePagamento) => {
    const pedido = {
        id: generateRandomId(),
        criadoEm: new Date(),
        atualizadoEm: new Date(),
        cliente: _cliente,
        pedido: _pedido,
        formaDePagamento: _formaDePagamento
    };
    _newPedido(pedido);
    return { type: NEW_PEDIDO, pedido };
}

export const updatePedido = (_id, _pedido) => {
    const pedido = Object.assign({}, _pedido, { atualizadoEm: new Date() });
    _updatePedido(pedido.id,pedido);
    return { type: UPDATE_PEDIDO, pedido, id: _id };
}

export const removePedido = (_id) => {
    _removePedido(_id);
    return { type: REMOVE_PEDIDO, id: _id };
}


