import {
    GET_PEDIDOS,
    NEW_PEDIDO,
    UPDATE_PEDIDO,
    REMOVE_PEDIDO,
    GET_PEDIDO
} from '../actions/types';

export default (state = {}, action) => {
    switch(action.type){
        case GET_PEDIDOS:
            return { ...state, pedidos: action.pedidos }
        case GET_PEDIDO:
            return { ...state, pedido: action.pedido }
        case NEW_PEDIDO:
            return { ...state, pedidos: state.pedidos.concat([action.pedido]) }
        case UPDATE_PEDIDO:
            return { ...state, pedidos: state.pedidos.map(_pedido => (_pedido.id === action.id) ? action.pedido : _pedido ), pedido: action.pedido }
        case REMOVE_PEDIDO:
            return { ...state, pedidos: state.pedidos.filter(_pedido => (_pedido.id !== action.id)), pedido: null }
        default:
            return state;
    }
}


