import React from 'react';
import { connect } from 'react-redux';
import * as actions from '../../actions';
import moment from 'moment';

const formatMoney = (valor) => 'R$ '+Number(valor).toFixed(2).replace('.',',');

class ListaDePedidos extends React.Component {

    componentDidMount(){
        this.props.getPedidos()
    }

    render(){
        const pedidos = (this.props.pedidos || []).slice();
        return (
            <div className="ListaDePedidos container">
                <hr />
                <br />
                {
                    pedidos
                    .sort((a,b) => new Date(a.criadoEm) < new Date(b.criadoEm) )
                    .map((_pedido) => (
                        <div key={_pedido.id} className="Pedido flex vertical">
                            <div className="flex-1"  onClick={()=>this.props.getPedido(_pedido)}>
                                <span>{ _pedido.cliente.nome } - { formatMoney(_pedido.formaDePagamento.valorTotal) }</span>
                                <br />
                                <small>Data: { moment(_pedido.criadoEm).format('DD/MM/YYYY HH:mm') }</small>
                                <hr />
                            </div>
                        </div>
                    ))
                }
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    pedidos: state.pedidos.pedidos
})

export default connect(mapStateToProps, actions)(ListaDePedidos)


